<template>
  <div class="home">
    <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="svg-filters">
      <defs>
        <filter id="filter-glitch-3">
          <feTurbulence type="fractalNoise" baseFrequency="0.000001" numOctaves="1" result="warp" ref="turb" />
          <feOffset dx="-90" dy="-90" result="warpOffset" />
          <feDisplacementMap xChannelSelector="R" yChannelSelector="G" scale="30" in="SourceGraphic" in2="warpOffset" />
        </filter>
      </defs>
    </svg>
    <Card :setting="cardSettings[0]" class="snow">
      <div></div>
    </Card>
    <Card class="beach" :setting="cardSettings[1]"><div>webgl</div></Card>
    <Card class="fall" :setting="cardSettings[2]"> 213</Card>
    <Card class="fire" :setting="cardSettings[3]">123 </Card>
  </div>
</template>

<script>
import { TimelineLite, Power0 } from 'gsap';
import { reactive, ref, onMounted } from 'vue';
import Card from './components/card';

const windHeight = document.body.clientHeight;

const getCardSetting = function(color, zIndex, top, height, url) {
  return {
    color,
    zIndex,
    top,
    height,
    url,
  };
};
export default {
  components: { Card },
  setup() {
    const cards = ref([]);
    const height = 0.76 * windHeight;
    const revalHeight = (windHeight - height) / 3;
    const cardSettings = reactive([
      getCardSetting(['#283048', '#859398'], 4, 0, height),
      getCardSetting(['#000046', '#1CB5E0'], 3, revalHeight, height),
      getCardSetting(['#44a08d', '#093637'], 2, revalHeight * 2, height),
      getCardSetting(['#dd5e89', '#f7bb97'], 1, revalHeight * 3, height),
    ]);
    const turb = ref(null);
    onMounted(() => {
      const turbVal = { val: 0.000001 };
      const turbValX = { val: 0.000001 };

      const btTl = new TimelineLite({
        paused: false,
        onUpdate() {
          turb.value.setAttribute('baseFrequency', `${turbVal.val} ${turbValX.val}`);
        },
        onComplete() {
          btTl.reverse();
        },
        onReverseComplete() {
          btTl.restart();
        },
      });
      console.log(turb.value, Power0);
      btTl.to(turbValX, 5, { val: 0.04, ease: Power0.easeNone }, 0);
      btTl.to(turbVal, 5, { val: 0.2, ease: Power0.easeNone }, 0);
      console.log(turb.value, Power0);
      // const btTl2 = new TimelineLite({
      //   onUpdate() {
      //     turb.value.setAttribute('baseFrequency', `${turbVal.val} ${turbValX.val}`);
      //   },
      // });
      // btTl2.to(turbVal, 0.1, { val: 0.000001 });
      // btTl2.to(turbValX, 0.1, { val: 0.000001 }, 0);
    });
    // 暴露给模板
    return {
      turb,
      cards,
      cardSettings,
    };
  },
};
</script>
<style lang="less" scoped>
.backgroundImge(@url) {
  width: 100%;
  height: 100%;
  background-image: url(@url);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  filter: url('#filter-glitch-3');
}
.home {
  height: 100%;
  width: 100%;
  .snow {
    .backgroundImge('../../../assets/snow.jpg');
  }
  .beach {
    .backgroundImge('../../../assets/beach.jpg');
  }
  .fall {
    .backgroundImge('../../../assets/fall.jpg');
  }
  .fire {
    .backgroundImge('../../../assets/fire.jpg');
  }
}
</style>
